<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <el-container>
      <el-header>
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-row type="flex">
            <el-col :span="5">
              <el-menu-item index="/">
                <el-image
                  style="width: 200px; height: auto;margin-left:25px"
                  src="//static2.cnodejs.org/public/images/cnodejs_light.svg"
                ></el-image>
              </el-menu-item>
            </el-col>
            <el-menu-item
              :index="/topiclist/+item.name"
              v-for="(item,index) in topics"
              :key="index"
            >{{item.desc}}</el-menu-item>
            <el-col :span="2" :offset="6">
              <el-menu-item index @click="dialogFormVisible = true" v-if="isLogined==false">登录</el-menu-item>

              <el-submenu index v-else>
                <template slot="title">
                  {{loginname}}
                  <el-badge :value="badgeText" :hidden="badgeText<=0" class="item"></el-badge>
                </template>

                <el-menu-item index="/center">
                  <i class="el-icon-user-solid"></i>个人中心
                </el-menu-item>
                <el-menu-item index="/msg">
                  <i class="el-icon-chat-dot-round"></i>消息通知
                  <el-badge v-show="badgeText>0" is-dot class="item"></el-badge>
                </el-menu-item>
                <el-menu-item index="/create">
                  <i class="el-icon-edit-outline"></i>发表话题
                </el-menu-item>
                <el-menu-item index="/logout">
                  <i class="el-icon-switch-button"></i>退出
                </el-menu-item>
              </el-submenu>
            </el-col>
          </el-row>
        </el-menu>
      </el-header>

      <!-- <el-main> -->
      <router-view />
      <!-- </el-main> -->
    </el-container>

    <!-- 登录框 -->
    <el-dialog title="登录" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="accesstoken" :label-width="formLabelWidth">
          <el-input v-model="form.accesstoken" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false;loging()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 错误提示 -->
    <el-alert v-show="isShowToast" :title="toastText" type="error"></el-alert>

    <el-backtop :bottom="100">
      <div
        style="{
        height: 100%;
        width: 100%;
        background-color: #f2f5f6;
        box-shadow: 0 0 6px rgba(0,0,0, .12);
        text-align: center;
        line-height: 40px;
        color: #1989fa;
      }"
      >
        <i class="el-icon-top"></i>
      </div>
    </el-backtop>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "App",
  data() {
    return {
      badgeText: "",
      activeIndex: "/topiclist/all",
      isShowToast: false,
      toastText: "",
      dialogFormVisible: false,
      form: {
        accesstoken: ""
      },
      formLabelWidth: "120px"
    };
  },
  created() {
    //读取localStorage数据
    var loginStatus = window.localStorage.getItem("loginStatus");
    if (loginStatus) {
      //执行用户处理登录状态
      this.mutationLogin();
      //为了再次刷新页面的时候state里面的loginname仍然有值
      this.mutationLoginname(window.localStorage.getItem("loginname"));
      this.mutationAccesstoken(window.localStorage.getItem("accesstoken"));
      this.mutationAuthorId(window.localStorage.getItem("author_id"));
    }
    if (this.isLogined) {
      //  执行ajax请求 获取未读消息
      this.getUnreadmMsgCount();
      var that = this;
      window.setInterval(function() {
        that.getUnreadmMsgCount();
      }, 1000 * 60 * 3);
    }
  },
  computed: {
    ...mapState(["topics", "isLogined", "loginname"]) //辅助函数
  },
  methods: {
    ...mapMutations([
      "mutationLogin",
      "mutationLoginname",
      "mutationAccesstoken",
      "mutationAuthorId"
    ]),
    // 获取服务器端未读消息个数
    getUnreadmMsgCount: function() {
      var that = this;
      this.$http
        .get("/message/count", {
          params: {
            accesstoken: this.$store.state.accesstoken
          }
        })
        .then(function(res) {
          console.log(res.data.data);
          if (res.data && res.data.success) {
            that.badgeText = res.data.data;
          }
        });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      //实现路由跳转
      this.$router.push("" + keyPath);
      console.log(this.$router);
      console.log(this.$route);
    },
    // 登录操作
    loging() {
      console.log("ok");
      var that = this;
      this.$http
        .post("/accesstoken", {
          accesstoken: this.form.accesstoken
        })
        .then(function(res) {
          // console.log(res.data)
          // 验证成功后  登录成功  修改vuex的登录状态isLogined的值
          that.mutationLogin();
          that.mutationLoginname(res.data.loginname);
          that.mutationAccesstoken(that.form.accesstoken);
          that.mutationAuthorId(res.data.id);

          // vuex中的数据保存在内存中的  当前应用程序生命周期结束是 vuex中的数据就会丢失
          //用户持久化登录  本地存储  localStorage  sessionStorage

          //保存登录后的数据到本地存储当中
          window.localStorage.setItem("loginStatus", true);
          window.localStorage.setItem("loginname", res.data.loginname);
          window.localStorage.setItem("accesstoken", that.form.accesstoken);

          window.localStorage.setItem("author_id", res.data.id);
          // 实现页面跳转 (个人中心)
          that.$router.push("/user/" + res.data.loginname);
          that.$router.go(0);
        })
        .catch(function(error) {
          // 请求失败时会在catch处理错误
          console.log(error.response);
          that.isShowToast = true;
          that.toastText = error.response.data.error_msg;
          that.$notify.error({
            title: "错误",
            message: error.response.data.error_msg
          });
        });
    }
  }
};
</script>

<style>
.el-header {
  padding: 0;
}
.el-submenu {
  width: 140px;
}
</style>



