<template>
  <div>
    <el-main>
      <el-tabs :tab-position="tabPosition">
        <el-tab-pane label="未读消息">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>未读消息</span>
              <el-button
                style="float: right; padding: 3px 0"
                type="text"
                v-if="hasNotReadMsg && hasNotReadMsg.length > 0"
                class="a"
                href
                @click.prevent="markAll"
              >标记全部消息为已读</el-button>
              <el-tag  effect="plain" v-else >暂无未读消息</el-tag>
            </div>

            <div v-for="(item,index) in hasNotReadMsg" :key="index" class="text item">
              <el-link
                type="primary"
                :href="'/#/user/'+item.author.loginname"
              >{{item.author.loginname}}</el-link>回复了你的
              <el-link type="info" :href="'/#/topic/'+item.topic.id">{{ item.topic.title }}</el-link>
              <el-button
                style="float: right; padding: 10px 0"
                type="success"
                plain
                @click.native="markRead(item.id)"
              >标记已读</el-button>
              <el-divider></el-divider>
            </div>
          </el-card>
        </el-tab-pane>
        <el-tab-pane label="已读消息">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>已读消息</span>
            </div>
            <div v-for="(item,index) in hasReadMsg" :key="index" class="text item">
              <el-link
                type="primary"
                :href="'/#/user/'+item.author.loginname"
              >{{item.author.loginname}}</el-link>回复了你的
              <el-link type="info" :href="'/#/topic/'+item.topic.id">{{ item.topic.title }}</el-link>
              <el-divider></el-divider>
            </div>
          </el-card>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "msg",
  data() {
    return {
      tabPosition: "left",
      selectedTabIndex: 0,
      hasReadMsg: null,
      hasNotReadMsg: null
    };
  },
  computed: {
    ...mapState(["topics", "isLogined"]) //辅助函数
  },
  created: function() {
    if (this.isLogined == false) {
      this.$message.error("您还未登录");
      return;
    }
    //页面加载完成后  执行ajax请求  获取消息数据
    var that = this;
    this.$http
      .get("/messages", {
        params: {
          accesstoken: this.$store.state.accesstoken
        }
      })
      .then(function(res) {
        console.log(res.data);
        that.hasReadMsg = res.data.data.has_read_messages;
        that.hasNotReadMsg = res.data.data.hasnot_read_messages;
      });
  },
  methods: {
    // 标记单条消息为已读
    markRead: function(mid) {
      // console.log(mid)
      var that = this;
      this.$http
        .post("/message/mark_one/" + mid, {
          accesstoken: this.$store.state.accesstoken
        })
        .then(function(res) {
          console.log(res.data);
          if (res.data && res.data.success) {
            // 标记指定消息为已读
            console.log("标记已读成功");
            // 把未读消息标记为已读的消息删除
            that.$router.go(0);
          }
        });
    },
    // 标记全部消息为已读
    markAll: function() {
      var that = this;
      this.$http
        .post("/message/mark_all", {
          accesstoken: this.$store.state.accesstoken
        })
        .then(function(res) {
          // console.log(res.data);
          if (res.data && res.data.success) {
            // 标记全部消息为已读
            // console.log("标记全部成功");
            // 把全部未读消息标记为已读的消息删除
            that.$router.go(0);
          }
        });
    }
  }
};
</script>

<style scoped>
.el-tabs {
  padding: 20px;
  overflow: hidden;
  background-color: #fff;
}
</style>


