<template>
  <div>
    <el-main>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>{{loginname}}收藏的话题</span>
        </div>
        <div v-for="(item,index) in collect_topics" :key="index">
          <el-avatar shape="square" :size="40" :src="item.author.avatar_url"></el-avatar>
          <el-link type="primary" :href="'#/topic/'+item.id">{{item.title}}</el-link>
          <el-divider></el-divider>
        </div>
      </el-card>
    </el-main>
  </div>
</template>

<script>
export default {
  name: "collect",
  props: ["loginname"],
  data() {
    return {
      collect_topics: null,
      page: 1, //当前页数
      limit: 15 //每一页显示的主题数量
    };
  },
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      //获取用户收藏的主题信息
      vm.$http.get("/topic_collect/" + vm.loginname).then(function(res) {
        console.log(res.data.data);
        vm.collect_topics = res.data.data;
      });
    });
  },
  beforeRouteUpdate(to, from, next) {
    var that = this;
    //获取用户收藏的主题信息
    this.$http.get("/topic_collect/" + loginname).then(function(res) {
      console.log(res.data.data);
      that.collect_topics = res.data.data;
    });
    next();
  }
};
</script>

<style scoped>
</style>


