<template>
  <div v-if="userinfo">
    <el-container>
      <el-aside width="300px">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>个人信息</span>
          </div>
          <div>
            <el-row>
              <el-col :span="8">
                <el-avatar shape="square" :size="40" fit="fill" :src="userinfo.avatar_url"></el-avatar>
              </el-col>
              <el-col :span="16">{{userinfo.loginname}}</el-col>
            </el-row>
            <el-divider></el-divider>
            <span>积分：{{userinfo.score}}</span>
          </div>
        </el-card>
      </el-aside>
      <el-main>
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <el-breadcrumb separator="/" replace="true">
              <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="text item">{{userinfo.loginname}}</div>
          <div class="text item">
            <el-image style="width: 50px; height: 50px" :src="userinfo.avatar_url"></el-image>
          </div>
          <div class="text item">用户积分：{{userinfo.score}}</div>
          <div class="text item" v-if="collect_topics">
            <a :href="'#/collect/'+loginname">{{ collect_topics.length }}个话题被收藏</a>
          </div>
          <div>
            <a :href="'http://github.com/'+userinfo.githubUsername">@ {{ userinfo.githubUsername }}</a>
          </div>
        </el-card>

        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>最近创建的话题</span>
          </div>
          <div v-for="(item,index) in userinfo.recent_topics" :key="index">
            <el-row type="flex" align="bottom">
              <el-col :span="1">
                <el-avatar shape="square" :size="40" :src="item.author.avatar_url"></el-avatar>
              </el-col>
              <el-col :span="17">
                <el-link type="primary" :href="'#/topic/'+item.id">{{item.title}}</el-link>
              </el-col>
              <el-col :span="1">
                <el-avatar shape="square" :size="30" :src="item.author.avatar_url"></el-avatar>
              </el-col>
              <el-col :span="5">{{item.last_reply_at}}</el-col>
            </el-row>
            <el-divider></el-divider>
          </div>
        </el-card>

        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>最近参与的话题</span>
          </div>
          <div v-for="(item,index) in userinfo.recent_replies" :key="index">
            <el-row type="flex" align="bottom">
              <el-col :span="1">
                <el-avatar shape="square" :size="40" :src="item.author.avatar_url"></el-avatar>
              </el-col>
              <el-col :span="17">
                <el-link type="primary" :href="'#/topic/'+item.id">{{item.title}}</el-link>
              </el-col>
              <el-col :span="1">
                <el-avatar shape="square" :size="30" :src="item.author.avatar_url"></el-avatar>
              </el-col>
              <el-col :span="5">{{item.last_reply_at}}</el-col>
            </el-row>
            <el-divider></el-divider>
          </div>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "user",
  props: ["loginname"],
  data() {
    return {
      userinfo: null,
      collect_topics: null
    };
  },
  components: {},
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      vm.$http.get("/user/" + to.params.loginname).then(function(res) {
        console.log(res.data);
        vm.userinfo = res.data.data;
      });
      //获取用户收藏的主题信息
      vm.$http.get("/topic_collect/" + to.params.loginname).then(function(res) {
        console.log(res.data);
        vm.collect_topics = res.data.data;
      });
    });
  },
  beforeRouteUpdate(to, from, next) {
    var that = this;
    this.$http.get("/user/" + to.params.loginname).then(function(res) {
      console.log(res.data);
      that.userinfo = res.data.data;
    });
    //获取用户收藏的主题信息
    this.$http.get("/topic_collect/" + to.params.loginname).then(function(res) {
      console.log(res.data);
      that.collect_topics = res.data.data;
    });
    next();
  }
};
</script>

<style scoped>
.el-aside {
  padding: 20px;
}
.item {
  margin-bottom: 18px;
}
.el-main .el-card {
  margin-bottom: 15px;
}
</style>



